"use client"

import { useState, useEffect } from "react"
import ImageUploader from "@/components/image-uploader"
import ExitForm from "@/components/exit-form"
import ExitsList from "@/components/exits-list"
import ImageOverlay from "@/components/image-overlay"
import PositionMapper from "@/components/position-mapper"
import ShiftSelector from "@/components/shift-selector"
import LoginPage from "@/components/login-page"

type ShiftsData = {
  [key: number]: { [key: number]: string }
}

type User = {
  id: string
  name: string
  role: "admin" | "user"
  assignedShift: number
}

export default function Home() {
  const [user, setUser] = useState<User | null>(null)
  const [image1, setImage1] = useState<string | null>(null)
  const [image2, setImage2] = useState<string | null>(null)
  const [currentShift, setCurrentShift] = useState<number | null>(null)
  const [shiftsData, setShiftsData] = useState<ShiftsData>({
    1: {},
    2: {},
    3: {},
    4: {},
  })
  const [selectedExit, setSelectedExit] = useState<number | null>(null)
  const [exitPositions1, setExitPositions1] = useState<{ [key: number]: { x: number; y: number } }>({})
  const [exitPositions2, setExitPositions2] = useState<{ [key: number]: { x: number; y: number } }>({})
  const [isMapping1, setIsMapping1] = useState(false)
  const [isMapping2, setIsMapping2] = useState(false)

  useEffect(() => {
    const savedUser = localStorage.getItem("currentUser")
    const savedShiftsData = localStorage.getItem("shiftsData")
    const savedExitPositions1 = localStorage.getItem("exitPositions1")
    const savedExitPositions2 = localStorage.getItem("exitPositions2")
    const savedImage1 = localStorage.getItem("emergencyImage1")
    const savedImage2 = localStorage.getItem("emergencyImage2")

    if (savedUser) setUser(JSON.parse(savedUser))
    if (savedShiftsData) setShiftsData(JSON.parse(savedShiftsData))
    if (savedExitPositions1) setExitPositions1(JSON.parse(savedExitPositions1))
    if (savedExitPositions2) setExitPositions2(JSON.parse(savedExitPositions2))
    if (savedImage1) setImage1(savedImage1)
    if (savedImage2) setImage2(savedImage2)
  }, [])

  useEffect(() => {
    localStorage.setItem("shiftsData", JSON.stringify(shiftsData))
  }, [shiftsData])

  useEffect(() => {
    localStorage.setItem("exitPositions1", JSON.stringify(exitPositions1))
  }, [exitPositions1])

  useEffect(() => {
    localStorage.setItem("exitPositions2", JSON.stringify(exitPositions2))
  }, [exitPositions2])

  useEffect(() => {
    if (user) setCurrentShift(user.assignedShift)
  }, [user])

  const exits = currentShift ? shiftsData[currentShift] || {} : {}

  const handleLogin = (loginUser: User) => {
    setUser(loginUser)
    localStorage.setItem("currentUser", JSON.stringify(loginUser))
    setCurrentShift(loginUser.assignedShift)
  }

  const handleLogout = () => {
    setUser(null)
    localStorage.removeItem("currentUser")
    setCurrentShift(null)
  }

  const handleImageUpload1 = (imageData: string) => {
    setImage1(imageData)
    setIsMapping1(true)
    localStorage.setItem("emergencyImage1", imageData)
  }

  const handleImageUpload2 = (imageData: string) => {
    setImage2(imageData)
    setIsMapping2(true)
    localStorage.setItem("emergencyImage2", imageData)
  }

  const handleExitNameChange = (exitNumber: number, name: string) => {
    setShiftsData((prev) => ({
      ...prev,
      [currentShift!]: {
        ...prev[currentShift!],
        [exitNumber]: name,
      },
    }))
  }

  const handlePositionSet1 = (positions: { [key: number]: { x: number; y: number } }) => {
    setExitPositions1(positions)
  }

  const handlePositionSet2 = (positions: { [key: number]: { x: number; y: number } }) => {
    setExitPositions2(positions)
  }

  const handleShiftChange = (shiftNumber: number) => {
    setCurrentShift(shiftNumber)
    setSelectedExit(null)
  }

  if (!user) {
    return <LoginPage onLogin={handleLogin} />
  }

  if (user.role === "admin") {
    return (
      <main className="min-h-screen bg-background">
        <div className="container mx-auto px-4 py-8">
          <div className="flex justify-between items-center mb-8">
            <div>
              <h1 className="text-4xl font-bold text-foreground mb-2">Plan de Emergencia - Admin</h1>
              <p className="text-muted-foreground">Gestiona los puntos de salida de emergencia por turno</p>
            </div>
            <div className="text-right">
              <p className="text-sm text-muted-foreground mb-2">Admin: {user.name}</p>
              <button
                onClick={handleLogout}
                className="px-4 py-2 bg-destructive text-destructive-foreground rounded hover:bg-destructive/90"
              >
                Cerrar sesión
              </button>
            </div>
          </div>

          <ShiftSelector currentShift={currentShift} onShiftChange={handleShiftChange} shiftsData={shiftsData} />

          <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
            <div className="lg:col-span-2 space-y-8">
              <ImageUploader onImageUpload={handleImageUpload1} label="Subir imagen para puntos 1-8" />
              <ImageUploader onImageUpload={handleImageUpload2} label="Subir imagen para puntos 9-10" />

              {image1 && isMapping1 && (
                <PositionMapper
                  image={image1}
                  onPositionSet={handlePositionSet1}
                  onMappingComplete={() => setIsMapping1(false)}
                  startNumber={1}
                />
              )}

              {image2 && isMapping2 && (
                <PositionMapper
                  image={image2}
                  onPositionSet={handlePositionSet2}
                  onMappingComplete={() => setIsMapping2(false)}
                  startNumber={9}
                />
              )}

              {image1 && !isMapping1 && (
                <div className="bg-card rounded-lg p-6 border border-border">
                  <div className="flex justify-between items-center mb-4">
                    <h2 className="text-xl font-semibold text-foreground">
                      Vista previa del plano - Puntos 1-8 - Turno {currentShift}
                    </h2>
                    <button
                      onClick={() => setIsMapping1(true)}
                      className="text-sm px-3 py-1 bg-primary text-primary-foreground rounded hover:bg-primary/90"
                    >
                      Ajustar posiciones
                    </button>
                  </div>
                  <ImageOverlay
                    image={image1}
                    exits={exits}
                    selectedExit={selectedExit}
                    onExitClick={setSelectedExit}
                    exitPositions={exitPositions1}
                    startNumber={1}
                  />
                </div>
              )}

              {image2 && !isMapping2 && (
                <div className="bg-card rounded-lg p-6 border border-border">
                  <div className="flex justify-between items-center mb-4">
                    <h2 className="text-xl font-semibold text-foreground">
                      Vista previa del plano - Puntos 9-10 - Turno {currentShift}
                    </h2>
                    <button
                      onClick={() => setIsMapping2(true)}
                      className="text-sm px-3 py-1 bg-primary text-primary-foreground rounded hover:bg-primary/90"
                    >
                      Ajustar posiciones
                    </button>
                  </div>
                  <ImageOverlay
                    image={image2}
                    exits={exits}
                    selectedExit={selectedExit}
                    onExitClick={setSelectedExit}
                    exitPositions={exitPositions2}
                    startNumber={9}
                  />
                </div>
              )}
            </div>

            <div className="space-y-6">
              <ExitForm
                onExitNameChange={handleExitNameChange}
                exits={exits}
                selectedExit={selectedExit}
                onSelectExit={setSelectedExit}
              />
              <ExitsList exits={exits} />
            </div>
          </div>
        </div>
      </main>
    )
  }

  return (
    <main className="min-h-screen bg-background">
      <div className="container mx-auto px-4 py-8">
        {currentShift === null ? (
          <>
            <div className="max-w-2xl mx-auto mb-12">
              <h1 className="text-4xl font-bold text-foreground mb-2 text-center">Plan de Emergencia</h1>
              <p className="text-muted-foreground text-center">Selecciona tu turno para ver el plano de salida</p>
            </div>

            <div className="max-w-4xl mx-auto">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                {[1, 2, 3, 4].map((shift) => (
                  <button
                    key={shift}
                    onClick={() => setCurrentShift(shift)}
                    className="group relative h-48 rounded-lg overflow-hidden border-2 border-border hover:border-primary transition-all hover:shadow-lg bg-card"
                  >
                    <div className="absolute inset-0 bg-gradient-to-br from-primary/10 to-primary/5 group-hover:from-primary/20 group-hover:to-primary/10 transition-all"></div>
                    <div className="relative h-full flex flex-col items-center justify-center">
                      <p className="text-sm text-muted-foreground mb-3 group-hover:text-foreground transition-colors">
                        Turno
                      </p>
                      <p className="text-7xl font-bold text-primary">{shift}</p>
                      {shiftsData[shift] && Object.keys(shiftsData[shift]).length > 0 && (
                        <p className="text-xs text-muted-foreground mt-4 group-hover:text-foreground transition-colors">
                          {Object.keys(shiftsData[shift]).length}/10 puntos configurados
                        </p>
                      )}
                    </div>
                  </button>
                ))}
              </div>
            </div>

            <div className="text-center mt-12">
              <a href="/admin" className="text-sm text-primary hover:text-primary/80 underline transition-colors">
                Acceso administrador
              </a>
            </div>
          </>
        ) : (
          <div className="max-w-5xl mx-auto">
            <button
              onClick={() => {
                setCurrentShift(null)
                setSelectedExit(null)
              }}
              className="mb-6 px-4 py-2 bg-secondary text-secondary-foreground rounded hover:bg-secondary/90 transition-colors"
            >
              Volver a seleccionar turno
            </button>

            <div className="bg-card rounded-lg p-8 border border-border">
              <div className="text-center mb-8">
                <p className="text-muted-foreground text-sm mb-2">Turno seleccionado</p>
                <p className="text-6xl font-bold text-primary">{currentShift}</p>
              </div>

              <div className="space-y-8">
                {image1 && (
                  <div className="bg-background rounded-lg p-6 border border-border">
                    <h2 className="text-lg font-semibold text-foreground mb-4">Plano de salida - Puntos 1-8</h2>
                    <ImageOverlay
                      image={image1}
                      exits={exits}
                      selectedExit={selectedExit}
                      onExitClick={setSelectedExit}
                      exitPositions={exitPositions1}
                      startNumber={1}
                    />
                  </div>
                )}

                {image2 && (
                  <div className="bg-background rounded-lg p-6 border border-border">
                    <h2 className="text-lg font-semibold text-foreground mb-4">Plano de salida - Puntos 9-10</h2>
                    <ImageOverlay
                      image={image2}
                      exits={exits}
                      selectedExit={selectedExit}
                      onExitClick={setSelectedExit}
                      exitPositions={exitPositions2}
                      startNumber={9}
                    />
                  </div>
                )}
              </div>

              {exits && Object.keys(exits).length > 0 && (
                <div className="mt-8">
                  <h3 className="text-lg font-semibold text-foreground mb-4">Puntos de salida</h3>
                  <div className="grid grid-cols-2 md:grid-cols-5 gap-4">
                    {Array.from({ length: 10 }, (_, i) => i + 1).map((exitNum) => (
                      <div
                        key={exitNum}
                        className={`bg-background border rounded p-4 text-center cursor-pointer transition-all ${
                          selectedExit === exitNum
                            ? "border-primary bg-primary/5"
                            : "border-border hover:border-primary"
                        }`}
                        onClick={() => setSelectedExit(exitNum)}
                      >
                        <p className="text-sm text-muted-foreground mb-2">Salida {exitNum}</p>
                        <p className="font-semibold text-foreground">{exits[exitNum] || "—"}</p>
                      </div>
                    ))}
                  </div>
                </div>
              )}
            </div>
          </div>
        )}
      </div>
    </main>
  )
}
